</template>

<script>
export default {
  data() {
    return {
      keyword: '',
      userInfo: uni.getStorageSync('userInfo') || {},
      defaultAvatar: '/static/logo.png',
      stats: {},
      showModal: false,
      creating: false,
      newUser: { username: '', password: '', role: 'customer' },
      roleOptions: ['ÊôÆÈÄöÁî®Êà∑', 'ÁÆ°ÁêÜÂëò'],
      roleIndex: 0,
      hotProducts: [],
      hotBrands: [],
      hotCategories: []
    };
  },
  onLoad() {
    this.loadStats();
    this.loadBoards();
  },
  methods: {
    onSearch() {
      uni.showToast({ title: this.keyword ? 'ÊêúÁ¥¢Ôºö' + this.keyword : 'ËØ∑ËæìÂÖ•ÂÖ≥ÈîÆËØç', icon: this.keyword ? 'none' : 'none' });
    },
    goProfile() {
      uni.navigateTo({ url: '/pages/account/profile' });
    },
    async loadStats() {
      try {
        // ‰ΩøÁî®Êñ∞ÁöÑÁªüËÆ°Êé•Âè£
        const res = await this.$api.statistics.getSystemStats();
        const r = res[1] || res;
        if (r && r.statusCode >= 200 && r.statusCode < 300) {
          this.stats = r.data || {};
        } else {
          uni.showToast({ title: r?.data?.error || 'Âä†ËΩΩÁªüËÆ°Â§±Ë¥•', icon: 'none' });
        }
      } catch (e) {
        console.error('Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', e);
        // Â¶ÇÊûúAPIÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§Êï∞ÊçÆ
        this.stats = {
          userCount: 0,
          productCount: 0,
          orderCount: 0,
          totalSales: 0
        };
      }
    },
    async loadBoards() {
      try {
        // Âπ∂Ë°åËé∑ÂèñÊâÄÊúâÁªüËÆ°Êï∞ÊçÆ
        const [hotProductsRes, hotCategoriesRes, hotBrandsRes] = await Promise.all([
          this.$api.statistics.getHotProducts({ period: 'week', limit: 5 }),
          this.$api.statistics.getHotCategories({ period: 'week', limit: 5 }),
          this.$api.brands.getBrandStats({ period: 'week' })
        ]);
        
        // Â§ÑÁêÜÁÉ≠ÂçñÂïÜÂìÅÊï∞ÊçÆ
        const hotProducts = hotProductsRes[1]?.data || hotProductsRes?.data || [];
        this.hotProducts = hotProducts;
        
        // Â§ÑÁêÜÁÉ≠ÂçñÂàÜÁ±ªÊï∞ÊçÆ
        const hotCategories = hotCategoriesRes[1]?.data || hotCategoriesRes?.data || [];
        this.hotCategories = hotCategories;
        
        // Â§ÑÁêÜÁÉ≠ÂçñÂìÅÁâåÊï∞ÊçÆ
        const hotBrands = hotBrandsRes[1]?.data || hotBrandsRes?.data || [];
        this.hotBrands = hotBrands;
        
      } catch (e) {
        console.error('Âä†ËΩΩÊ¶úÂçïÂ§±Ë¥•:', e);
        // Â¶ÇÊûúAPIÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§Êï∞ÊçÆ
        this.hotProducts = [];
        this.hotBrands = [];
        this.hotCategories = [];
      }
    },

    goToUserManage() { uni.navigateTo({ url: '/pages/management/user-manage' }); },
    goToProductManage() { uni.navigateTo({ url: '/pages/management/product-manage' }); },
    goToOrderManage() { uni.navigateTo({ url: '/pages/management/order-manage' }); },

    showCreateUserModal() { this.showModal = true; },
    hideModal() {
      this.showModal = false;
      this.newUser = { username: '', password: '', role: 'customer' };
      this.roleIndex = 0;
    },
    onRoleChange(e) {
      this.roleIndex = Number(e.detail.value || 0);
      this.newUser.role = this.roleIndex === 0 ? 'customer' : 'admin';
    },

    async createUser() {
      if (!this.newUser.username.trim() || !this.newUser.password) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜôÂÆåÊï¥‰ø°ÊÅØ', icon: 'none' });
        return;
      }
      this.creating = true;
      try {
        // Ëµ∞Áªü‰∏Ä API Â∞ÅË£ÖÔºàËá™Âä®Â∏¶ tokenÔºâ
        const res = await this.$api.admin.createUser(this.newUser);
        const r = res[1] || res;
        if (r && r.statusCode >= 200 && r.statusCode < 300) {
          uni.showToast({ title: 'Áî®Êà∑ÂàõÂª∫ÊàêÂäü', icon: 'success' });
          this.hideModal();
          this.loadStats();
        } else {
          uni.showToast({ title: r?.data?.error || 'ÂàõÂª∫Â§±Ë¥•', icon: 'none' });
        }
      } catch (e) {
        console.error('ÂàõÂª∫Áî®Êà∑Â§±Ë¥•:', e);
        uni.showToast({ title: 'ÁΩëÁªúÈîôËØØÔºåËØ∑ÈáçËØï', icon: 'none' });
      } finally {
        this.creating = false;
      }
    }
  }
};
</script>

